import React from 'react';
import { JSONLogReader } from '../component/FileZone';
import { Minimap } from './Minimap';
import { useStoreActions, useStoreState } from '../store';
import { EventInfo } from './EventInfo';

const Profile = () => {
  return (
    <div>
      <Minimap />
      <EventInfo />
    </div>
  );
};

export const Tracer: React.FC = () => {
  const { eventLoaded } = useStoreState(store => store.tracer);
  const loadEvents = useStoreActions(store => store.tracer.loadEvents);
  return (
    <div>
      {eventLoaded ? <JSONLogReader onLoad={loadEvents} /> : <Profile />}
    </div>
  );
};
