import {
  action,
  Action,
  computed,
  Computed,
  createStore,
  createTypedHooks,
} from 'easy-peasy';
import { TraceEvent } from '../types';
import { isEmpty } from 'lodash';

interface TracerModel {
  events: TraceEvent[];
  activeIndex: number | null;

  eventLoaded: Computed<TracerModel, boolean>;
  activeEvent: Computed<TracerModel, TraceEvent | null>;

  loadEvents: Action<TracerModel, TraceEvent[]>;
  active: Action<TracerModel, number>;
  inactive: Action<TracerModel>;
}

interface StoreModel {
  tracer: TracerModel;
}

const storeModel: StoreModel = {
  tracer: {
    events: [],
    activeIndex: null,
    eventLoaded: computed(state => isEmpty(state.events)),
    activeEvent: computed(state =>
      state.activeIndex === null ? null : state.events[state.activeIndex],
    ),

    loadEvents: action((state, events) => {
      state.events = events;
    }),

    active: action((state, index) => {
      state.activeIndex = index;
    }),

    inactive: action(state => {
      state.activeIndex = null;
    }),
  },
};

export const store = createStore(storeModel);

const typedHooks = createTypedHooks<StoreModel>();

export const useStoreActions = typedHooks.useStoreActions;
export const useStoreDispatch = typedHooks.useStoreDispatch;
export const useStoreState = typedHooks.useStoreState;
