import React, { useState } from 'react';
import { Group } from '@vx/group';
import { Circle } from '@vx/shape';
import { scaleLinear } from '@vx/scale';
import _ from 'lodash';
import { Tooltip, withTooltip } from '@vx/tooltip';
import { localPoint } from '@vx/event';
import { color } from 'd3-color';
import { withSize } from 'react-sizeme';
import { anyStrToColor } from '../helper';
import { useStoreActions, useStoreState } from '../store';

const EpochTooltip = ({ epoch }) => {
  return (
    <>
      <p>
        epoch <strong>{epoch}</strong>
      </p>
    </>
  );
};

const RoundTooltip = ({ epoch, round }) => {
  return (
    <>
      <p>
        epoch <strong>{epoch}</strong>
      </p>
      <p>
        round <strong>{round}</strong>
      </p>
    </>
  );
};

const MinimapTooltip = ({ left, top, event }) => {
  const { scope } = event;
  return (
    <Tooltip top={top} left={left} style={{ zIndex: '9999' }}>
      <h3 style={{ color: anyStrToColor(event.eventName) }}>
        {event.eventName}
      </h3>
      <small>{new Date(event.timestamp).toJSON().slice(11)}</small>
      {scope.type === 'epoch' && <EpochTooltip epoch={scope.epochId} />}
      {scope.type === 'round' && (
        <RoundTooltip epoch={scope.epochId} round={scope.roundId} />
      )}
    </Tooltip>
  );
};

export const Minimap = withSize()(
  withTooltip(props => {
    const {
      hideTooltip,
      showTooltip,
      tooltipOpen,
      tooltipLeft,
      tooltipTop,
      tooltipData,
      size,
    } = props;

    const events = useStoreState(store => store.tracer.events);
    const active = useStoreActions(actions => actions.tracer.active);

    const fill = '#242424';
    const width = size.width;
    const height = 50;
    const start = _.head(events).timestamp;
    const end = _.last(events).timestamp;

    const [highlight, setHighlight] = useState(null);

    const scale = scaleLinear({
      rangeRound: [0, width],
      domain: [_.clamp(start, 0, start - 500), end + 500],
      clamp: true,
    });

    return (
      <div>
        <svg width={width} height={height}>
          <rect width={width} height={height} fill={fill} />
          <Group>
            {events.map((event, i) => {
              const cx = scale(event.timestamp);
              const cy = height / 2;
              return (
                <Circle
                  style={{ cursor: 'pointer' }}
                  key={i}
                  cx={cx}
                  cy={cy}
                  fill={
                    highlight === i
                      ? color(anyStrToColor(event.eventName)).brighter()
                      : anyStrToColor(event.eventName)
                  }
                  r={5}
                  onMouseEnter={() => {
                    setHighlight(i);
                    showTooltip({
                      tooltipLeft: cx,
                      tooltipTop: cy + 20,
                      tooltipData: event,
                    });
                  }}
                  onMouseLeave={() => {
                    hideTooltip();
                    setHighlight(null);
                  }}
                  onClick={() => {
                    active(i);
                  }}
                />
              );
            })}
          </Group>
        </svg>

        {tooltipOpen && (
          <MinimapTooltip
            left={tooltipLeft}
            top={tooltipTop}
            event={tooltipData}
          />
        )}
      </div>
    );
  }),
);
