import React from 'react';
import { Table, Drawer } from 'antd';
import _ from 'lodash';
import { anyStrToColor } from '../helper';
import { useStoreActions, useStoreState } from '../store';
import { EventScope, TraceEvent } from '../types';
import { EventDetail } from './EventDetail';

function EventInfo() {
  const { events, activeEvent } = useStoreState(store => store.tracer);
  const { active, inactive } = useStoreActions(actions => actions.tracer);

  const columns = [
    {
      title: 'Name',
      dataIndex: 'eventName',
      render: (name: string, record: TraceEvent, index: number) => (
        <div style={{ cursor: 'pointer' }} onClick={() => active(index)}>
          <svg width="15" height="15">
            <rect fill={anyStrToColor(name)} width="15" height="15" />
          </svg>
          {name}
        </div>
      ),
    },
    {
      title: 'Epoch',
      dataIndex: 'scope',
      render: (scope: EventScope) => {
        return _.get(scope, 'epochId');
      },
    },
    {
      title: 'Round',
      dataIndex: 'scope',
      render: (scope: EventScope) => {
        return _.get(scope, 'roundId');
      },
    },
    {
      title: 'Time',
      dataIndex: 'timestamp',
      render: (timestamp: number) => {
        return new Date(timestamp).toJSON();
      },
    },
  ];

  return (
    <div style={{ position: 'relative', overflow: 'hidden' }}>
      <Table
        size="middle"
        rowKey={record => String(record.timestamp)}
        dataSource={events}
        columns={columns}
        pagination={false}
        scroll={{ y: 480 }}
      />
      <Drawer
        placement="right"
        width={400}
        onClose={() => inactive()}
        visible={!!activeEvent}
        getContainer={false}
        style={{ position: 'absolute' }}
      >
        <EventDetail />
      </Drawer>
    </div>
  );
}

export { EventInfo };
