import React, { useEffect, useMemo } from 'react';
import { useDropzone, DropzoneRootProps } from 'react-dropzone';
import { isEmpty } from 'lodash';
import { readJSONLog } from '../../helper';
import { TraceEvent } from '../../types';

const baseStyle = {
  flex: 1,
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  padding: '20px',
  borderWidth: 2,
  borderRadius: 2,
  borderColor: '#eeeeee',
  borderStyle: 'dashed',
  outline: 'none',
  transition: 'border .24s ease-in-out',
};

const activeStyle = {
  borderColor: '#2196f3',
};

const acceptStyle = {
  borderColor: '#00e676',
};

const rejectStyle = {
  borderColor: '#ff1744',
};

const example = [
  {
    timestamp: 1574041897764,
    eventName: 'start_epoch',
    scope: {
      type: 'epoch',
      epochId: 1,
    },
    tag: {
      key: 'start epoch',
      number: 0,
      null: null,
    },
  },

  {
    timestamp: 1574041897900,
    eventName: 'start_round',
    scope: {
      type: 'round',
      epochId: 1,
      roundId: 0,
    },
    tag: {
      key: 'start Round',
      number: 0,
      null: null,
    },
  },

  {
    timestamp: 1574041898500,
    eventName: 'end_round',
    scope: {
      type: 'round',
      epochId: 1,
      roundId: 0,
    },
    tag: {
      key: 'end roundddd',
      number: 0,
      null: null,
    },
  },

  {
    timestamp: 1574041898800,
    eventName: 'receive_proposal',
    scope: {
      type: 'round',
      epochId: 1,
      roundId: 0,
    },
    tag: {
      epoch_hash: '0x...',
      'lock.round': null,
      'lock.hash': null,
      other_object: {
        some_key: 'some value',
      },
    },
  },

  {
    timestamp: 1574041900964,
    eventName: 'end_epoch',
    scope: {
      type: 'epoch',
      epochId: 1,
    },
    tag: {
      key: 'end epoch aaa',
      number: 0,
      null: null,
    },
  },
];

const demo =
  'data:text/json;charset=utf-8,' + encodeURIComponent(JSON.stringify(example));

export function JSONLogReader({
  onLoad,
}: {
  onLoad: (events: TraceEvent[]) => void;
}) {
  const {
    getRootProps,
    getInputProps,
    isDragActive,
    isDragAccept,
    isDragReject,
    acceptedFiles,
  } = useDropzone({ accept: '.json', multiple: false });

  useEffect(() => {
    if (!isEmpty(acceptedFiles) && onLoad) {
      readJSONLog(acceptedFiles[0]).then(onLoad);
    }
  }, [acceptedFiles]);

  const style = useMemo(
    () => ({
      ...baseStyle,
      ...(isDragActive ? activeStyle : {}),
      ...(isDragAccept ? acceptStyle : {}),
      ...(isDragReject ? rejectStyle : {}),
    }),
    [isDragAccept, isDragActive, isDragReject],
  );

  return (
    <div className="container">
      <div {...getRootProps({ style } as DropzoneRootProps)}>
        <input {...getInputProps()} />
        <p>
          Drag a parsed <strong>events.json</strong> here, or click to select
          file
        </p>
      </div>
      <div>
        Or try this demo&nbsp;
        <a href={demo} download="events.json">
          events.json
        </a>
      </div>
    </div>
  );
}
